import React from 'react';
// Redux
import { useDispatch, useSelector } from 'react-redux';
import { get } from '../../../../redux/async/branchesAsync';
// MUI Components 
import { CircularProgress, Typography, TextField, MenuItem } from '@mui/material';

const Branch = (props) => {

    const dispatch = useDispatch();
    const branchRedux = useSelector((state) => state.branches.value);
    const [branch, setBranch] = React.useState(props.branch ?? "");

    const loading = branchRedux.loading;
    const status = branchRedux.status;

    const branchOptions = branchRedux.branches.map(data => {
        return {
            value: data.id,
            label: data.branch.toUpperCase()
        }
    });

    const handleChange = (value) => {

        setBranch(value)
        if (props.setBranch) {
            props.setBranch(value)
        }

        if (props.setLabel) {
            let label = branchRedux.branches.filter(data => {
                return value === data.id;
            });

            props.setLabel(label[0].branch)
        }
    }

    React.useEffect(() => {
        dispatch(get());
    }, [dispatch])

    return (
        <div>
            <TextField
                select={!loading}
                label="Branch"
                name="branch"
                size="small"
                variant="outlined"
                value={branch}
                onChange={(e) => handleChange(e.target.value)}
                fullWidth
                required
                disabled={loading}
                inputProps={{ MenuProps: { disableScrollLock: true } }}
                InputProps={{
                    endAdornment: loading &&
                        <Typography sx={{ width: '100%', textAlign: 'right' }}>
                            {status}
                            <CircularProgress size={15} style={{ marginRight: 15, marginLeft: 5 }} />
                        </Typography>
                }}
            >
                {branchOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                        {option.label}
                    </MenuItem>
                ))}
            </TextField>

        </div>
    )
}

export default Branch;