import React from 'react'; 
import toastService from '../../../services/toast/toastService';
import employeeRepository from '../../../repositories/employeeRepository'; 
// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import LoadingButton from '@mui/lab/LoadingButton';
import Paper from '@mui/material/Paper';
// Form Components
import Gender from './fields/Gender';
import CivilStatus from './fields/CivilStatus';
import Birthdate from './fields/Birthdate';
import ContactNumber from './fields/ContactNumber';
import { Divider } from '@mui/material';
import Roles from './fields/Roles';
import Positions from './fields/Positions';
import ProfilePhoto from '../shared/ProfilePhoto/ProfilePhoto';
import Email from './fields/Email';
import Branch from './fields/Branch';
// react-icons
import { FaCheck } from 'react-icons/fa';

export default function NewEmployeeForm(props) {
 
    const [loading, setLoading] = React.useState(false);

    // Basic Profile  
    const [avatar, setAvatar] = React.useState(null);
    const [avatarPreview, setAvatarPreview] = React.useState(null);
    const [employeeID, setEmployeeID] = React.useState("");
    const [email, setEmail] = React.useState("");
    const [role, setRole] = React.useState("");
    const [position, setPosition] = React.useState("");
    const [branch, setBranch] = React.useState("");
    const [lastName, setLastName] = React.useState("");
    const [firstName, setFirstName] = React.useState("");
    const [middleName, setMiddleName] = React.useState("");
    const [suffix, setSuffix] = React.useState("");
    const [birthdate, setBirthdate] = React.useState(null);
    const [genderValue, setGenderValue] = React.useState("");
    const [civilStatus, setCivilStatus] = React.useState("");
    const [contactNumber, setContactNumber] = React.useState("");

    const handleReset = () => {
        setAvatar(null);
        setAvatarPreview(null);
        setEmployeeID("");
        setEmail("");
        setRole("");
        setPosition("");
        setBranch("");
        setLastName("");
        setFirstName("");
        setMiddleName("");
        setSuffix("");
        setBirthdate(null);
        setGenderValue("");
        setCivilStatus("");
        setContactNumber("");
    }

    const handleSubmit = async (e) => {
        try {
            e.preventDefault();

            setLoading(true);

            let data = {
                avatar,
                employeeId: employeeID,
                email: email,
                role: role,
                position: position,
                branch: branch,
                firstName: firstName,
                lastName: lastName,
                middleName: middleName,
                suffix: suffix,
                birthdate: new Date(birthdate),
                gender: genderValue,
                civilStatus: civilStatus.toLowerCase(),
                contactNumber: contactNumber
            }

            let formData = new FormData();
            for (let key in data) {
                formData.append(key, data[key]);
            }

            let result = await employeeRepository.register(formData);

            if (!result.error) {
                toastService.success(result.message); 
                if(props.handleChange){
                    props.handleChange(0);
                }
            } else {
                toastService.error(result.message);
            }

        } catch (e) {
            toastService.error(e.toString());
        } finally {
            setLoading(false);
        }
    }

    return (
        <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
            <div>
                <Paper square sx={{ p: 3, pr: 5, m: 1, minHeight: 500, display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
                    <Grid container spacing={1}>
                        <Grid item xs={2.5}>
                            <ProfilePhoto width={220} height={200} avatarPreview={avatarPreview} setAvatar={setAvatar} setAvatarPreview={setAvatarPreview} loading={loading} />
                        </Grid>
                        <Grid item xs={9.5}>
                            <Grid container spacing={2}>
                                <Grid item xs={6}>
                                    <Roles roles={role} setRoles={setRole} />
                                </Grid>
                                <Grid item xs={6}>
                                    <Positions positions={position} setPositions={setPosition} />
                                </Grid>
                                <Grid item xs={6}>
                                    <Branch branch={branch} setBranch={setBranch} />
                                </Grid>
                                <Grid item xs={6}>
                                    <Email email={email} setEmail={setEmail} />
                                </Grid>
                                <Grid item xs={12}>
                                    <Divider sx={{ mt: 1, mb: 1 }} />
                                </Grid>
                                <Grid item xs={4}>
                                    <TextField
                                        onChange={(e) => setEmployeeID(e.target.value)}
                                        value={employeeID}
                                        variant="outlined"
                                        size="small"
                                        required
                                        fullWidth
                                        id="employeeID"
                                        label="Employee ID"
                                        placeholder="Employee ID"
                                        name="employeeID"
                                    />
                                </Grid>
                                <Grid item xs={4}>
                                    <TextField
                                        onChange={(e) => setLastName(e.target.value)}
                                        value={lastName}
                                        variant="outlined"
                                        size="small"
                                        required
                                        fullWidth
                                        id="lastName"
                                        label="Last Name"
                                        placeholder="Last Name"
                                        name="lastName"
                                    />
                                </Grid>
                                <Grid item xs={4}>
                                    <TextField
                                        onChange={(e) => setFirstName(e.target.value)}
                                        value={firstName}
                                        variant="outlined"
                                        size="small"
                                        required
                                        fullWidth
                                        id="firstName"
                                        label="First Name"
                                        placeholder="First Name"
                                        name="firstName"
                                    />
                                </Grid>
                                <Grid item xs={4}>
                                    <TextField
                                        onChange={(e) => setMiddleName(e.target.value)}
                                        value={middleName}
                                        variant="outlined"
                                        size="small"
                                        fullWidth
                                        id="middleName"
                                        label="Middle Name"
                                        placeholder="Middle Name"
                                        name="middleName"
                                    />
                                </Grid>
                                <Grid item xs={4}>
                                    <TextField
                                        onChange={(e) => setSuffix(e.target.value)}
                                        value={suffix}
                                        variant="outlined"
                                        size="small"
                                        fullWidth
                                        id="suffix"
                                        label="Name Suffix"
                                        placeholder="Name Suffix"
                                        name="suffix"
                                    />
                                </Grid>
                                <Grid item xs={4}>
                                    <Birthdate setBirthdate={setBirthdate} birthdate={birthdate} variant="outlined" />
                                </Grid>
                                <Grid item xs={4}>
                                    <Gender setGender={setGenderValue} gender={genderValue} variant="outlined" isRequired={true} />
                                </Grid>
                                <Grid item xs={4}>
                                    <CivilStatus setCivilStatus={setCivilStatus} civilStatus={civilStatus} variant="outlined" />
                                </Grid>
                                <Grid item xs={4}>
                                    <TextField
                                        onChange={(e) => setContactNumber(e.target.value)}
                                        value={contactNumber}
                                        variant="outlined"
                                        size="small"
                                        required
                                        fullWidth
                                        id="contactNumber"
                                        label="Contact Number"
                                        name="contactNumber"
                                        InputProps={{
                                            inputComponent: ContactNumber
                                        }}
                                    />
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>

                    <Box sx={{ mt: 2 }} display="flex" flexDirection="row" justifyContent="flex-end">
                        <LoadingButton size='large' loading={loading} variant="contained" onClick={handleSubmit}>
                            Register
                            <FaCheck style={{ marginLeft: 5 }} />
                        </LoadingButton>
                    </Box>
                </Paper>
            </div>
        </form>
    )
}
