import React from 'react'
// Modal Components
import InnerModalDialog from '../shared/Dialog/InnerModalDialog';
import ChangePositionForm from '../forms/ChangePositionForm';

export default function ChangePositionModal(props) { 
    return (
        <div>
            <InnerModalDialog open={props.open} title={"Change Position"} toggle={props.toggle}>
                <ChangePositionForm toggle={props.toggle} selectedData={props.selectedData} />
            </InnerModalDialog>
        </div>
    )
}
