import React from 'react';
import { useNavigate } from 'react-router-dom';
import { format, parseISO } from 'date-fns';
// Utilities  
import toastService from '../../../services/toast/toastService';
import config from '../../../config/config';
// Redux
import { useSelector, useDispatch } from 'react-redux';
import { get, search as searchEmployee } from '../../../redux/async/employeeAsync';
// MUI Components
import { DataGrid, GridActionsCellItem } from '@mui/x-data-grid';
import { Box, Typography, Avatar, TextField } from '@mui/material';
import { LoadingButton } from '@mui/lab';
// Page Components
import GridLoadingOverlay from '../grid/GridLoadingOverlay';
import GridNoRowOverlay from '../grid/GridNoRowOverlay';
import Toolbar from '../grid/Toolbar';
import EmployeeProfileModal from '../modals/EmployeeProfileModal';
import ChangeRoleModal from '../modals/ChangeRoleModal';
import ChangePositionModal from '../modals/ChangePositionModal';
import ChangeBranchModal from '../modals/ChangeBranchModal';
// react-icons
import { FaSlidersH, FaLightbulb, FaSearch, FaUserTie } from 'react-icons/fa';
import { GrPowerCycle } from 'react-icons/gr';

const LoadingOverlayOverride = () => {
    return (
        <GridLoadingOverlay />
    );
}

const NoRowOverlayOverride = () => {
    return (
        <GridNoRowOverlay />
    );
}

const ToolbarOverride = (props) => {
    return (
        <Toolbar
            showCreate={true}
            createEvent={props.create}
            createTitle="Register"
            showExport={true}
        />
    );
}

const ActionsIcon = () => {
    return <FaSlidersH />
}

export default function EmployeeDaTatable(props) {

    const dispatch = useDispatch();

    const employeesRedux = useSelector((state) => state.employees.value);
    const loading = employeesRedux.loading;

    const [selectedEmployee, setSelectedEmployee] = React.useState("");
    const [searching, setSearching] = React.useState(false);
    const [keyword, setKeyword] = React.useState("");
    const [showEmployeeModal, setShowEmployeeModal] = React.useState(false);
    const toggleEmployeeModal = () => setShowEmployeeModal(!showEmployeeModal);
    const [showChangeRoleModal, setShowChangeRoleModal] = React.useState(false);
    const toggleChangeRoleModal = () => setShowChangeRoleModal(!showChangeRoleModal);
    const [showChangePositionModal, setShowChangePositionModal] = React.useState(false);
    const toggleChangePositionModal = () => setShowChangePositionModal(!showChangePositionModal);
    const [showBranchModal, setShowBranchModal] = React.useState(false);
    const toggleChangeBranchModal = () => setShowBranchModal(!showBranchModal);


    const width = 200;

    const actions = [
        {
            label: "Employee Profile",
            icon: <FaUserTie size={18} />,
            event: (data) => handleSelectedEmployee(data),
        },
        {
            label: "Change Branch",
            icon: <GrPowerCycle size={18} />,
            event: (data) => handleChangeBranch(data),
        },
        {
            label: "Change Position",
            icon: <GrPowerCycle size={18} />,
            event: (data) => handleChangePosition(data),
        },
        {
            label: "Change Role",
            icon: <GrPowerCycle size={18} />,
            event: (data) => handleChangeRole(data),
        },
    ]


    const columns = [
        {
            field: 'avatar', headerName: 'Photo', width: 150, headerAlign: 'center', align: 'center', sortable: false, hide: true, renderCell: (params) => {

                let data = params.row;
                let avatar_path = null;

                if (data.avatar !== null) {
                    avatar_path = `${config.RESOURCES}users/${data.uuid}/files/avatar/${data.avatar}`;
                }

                return (
                    <div>
                        <Avatar src={avatar_path} />
                    </div>
                );
            }
        },
        { field: 'last_name', headerName: 'Last Name', width: width },
        { field: 'first_name', headerName: 'First Name', width: width },
        { field: 'email', headerName: 'Email', width: 350 },
        { field: 'contact_no', headerName: 'Contact #', hide: true, width: width },
        {
            field: 'birthdate', headerName: 'Birthdate', width: width, hide: true, renderCell: (params) => {
                return (
                    <div>
                        {format(parseISO(params.row.birthdate), "MMMM dd, yyyy")}
                    </div>
                );
            }
        },
        { field: 'branch', headerName: 'Branch', width: 150 },
        { field: 'position', headerName: 'Position', width: 150 },
        { field: 'role', headerName: 'Role', width: 150 },
        { field: 'gender', headerName: 'Gender', width: width, hide: true },
        { field: 'civil_status', headerName: 'Civil Status', width: width, hide: true },
        { field: 'nationality', headerName: 'Nationality', width: width, hide: true },
        {
            field: 'actions', headerName: 'Options', flex: true, type: 'actions', getActions: (params) => {
                return actions.map((data, index) => {
                    return <GridActionsCellItem
                        key={`gridCell-${index}`}
                        icon={data.icon}
                        onClick={() => data.event(params.row)}
                        label={data.label}
                        sx={{
                            '&:hover': {
                                color: data.color ?? "#00BFA5"
                            }
                        }}
                        showInMenu
                    />
                });
            }

        },
    ];

    const handleSelectedEmployee = (data) => {
        setSelectedEmployee(data);
        toggleEmployeeModal();
    }

    const handleChangeBranch = (data) => {
        setSelectedEmployee(data);
        toggleChangeBranchModal();
    }

    const handleChangePosition = (data) => {
        setSelectedEmployee(data);
        toggleChangePositionModal();
    }

    const handleChangeRole = (data) => {
        setSelectedEmployee(data);
        toggleChangeRoleModal();
    }

    const handlePrint = (data) => {
        console.log(data);
    }

    const handleRegister = () => {
        if (props.handleChange) {
            props.handleChange(1);
        }
    }

    const handleKeyword = (str) => {
        setKeyword(str);
        if (str === "") {
            loadEmployees();
        }
    }

    const handleSearch = async () => {
        try {
            if (keyword === "") {
                return false;
            }

            setSearching(true);

            let data = {
                name: keyword
            }

            await dispatch(searchEmployee(data)).unwrap();


        } catch (error) {
            toastService.error(error.toString());
        } finally {
            setSearching(false)
        }

    };


    const loadEmployees = React.useCallback(() => {
        dispatch(get());
    }, [dispatch])

    React.useEffect(() => {

        let mounted = false;

        if (!mounted) {
            loadEmployees();
        }

        return () => mounted = true;

    }, [loadEmployees]);

    return (
        <div style={{ height: 600, width: '100%' }}>
            {showEmployeeModal && <EmployeeProfileModal open={showEmployeeModal} toggle={toggleEmployeeModal} selectedData={selectedEmployee} />}
            {showChangeRoleModal && <ChangeRoleModal open={showChangeRoleModal} toggle={toggleChangeRoleModal} selectedData={selectedEmployee} />}
            {showChangePositionModal && <ChangePositionModal open={showChangePositionModal} toggle={toggleChangePositionModal} selectedData={selectedEmployee} />}
            {showBranchModal && <ChangeBranchModal open={showBranchModal} toggle={toggleChangeBranchModal} selectedData={selectedEmployee} />}
            <Box display="flex" flexDirectoin="row" justifyContent="space-between">
                <Typography variant="subtitle2" sx={{ mt: 1.5 }}>
                    <FaLightbulb size={14} style={{ marginRight: 2, color: "#00BFA5" }} />
                    Tips: Double-click a row to view member profile
                </Typography>
                <Box display="flex" flextDirection="row" >
                    <TextField
                        type="search"
                        variant="outlined"
                        size="small"
                        onChange={(e) => handleKeyword(e.target.value)}
                        placeholder="Search Member"
                        sx={{
                            mb: 1,
                            minWidth: 400
                        }}
                    />
                    <LoadingButton type="button" loading={searching} onClick={handleSearch} variant="contained" size="small" sx={{ height: 38, ml: 1, minWidth: 100 }}>
                        Search <FaSearch style={{ marginLeft: 7 }} />
                    </LoadingButton>
                </Box>
            </Box>
            <DataGrid
                style={{ height: 500 }}
                rows={employeesRedux.employees}
                columns={columns}
                onCellDoubleClick={(params) => handleSelectedEmployee(params.row)}
                loading={loading}
                density="standard"
                disableSelectionOnClick
                disableColumnMenu
                components={{
                    LoadingOverlay: LoadingOverlayOverride,
                    NoRowsOverlay: NoRowOverlayOverride,
                    Toolbar: ToolbarOverride,
                    MoreActionsIcon: ActionsIcon
                }}
                componentsProps={{
                    toolbar: {
                        create: () => { handleRegister() },
                    }
                }}
            />
        </div>
    );
}