import React from 'react';
import utilityService from '../../../../services/utils/utilityServices';
// Redux
import { useDispatch, useSelector } from 'react-redux';
import { get } from '../../../../redux/async/positionAsync';
// MUI Components
import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem'; 
import { Typography, CircularProgress } from '@mui/material';

const Positions = (props) => {

    const dispatch = useDispatch();
    const positionsRedux = useSelector((state) => state.positions.value);
    const [positions, setPositions] = React.useState(props.positions ?? "");

    const loading = positionsRedux.loading;
    const status = positionsRedux.status;

    const positionsOptions = positionsRedux.positions.map(data => {
        return {
            value: data.id,
            label: utilityService.capitalizeFirstLetter(data.position)
        }
    });

    const handleChange = (value) => {

        setPositions(value)
        if (props.setPositions) {
            props.setPositions(value)
        }

        if (props.setLabel) {
            let label = positionsRedux.positions.filter(data => {
                return value === data.id;
            });

            props.setLabel(label[0].position)
        }
    }

    React.useEffect(() => {
        dispatch(get());
    }, [dispatch])

    return (
        <div> 
            <TextField
                select={!loading}
                label={props.label ?? "Positions"}
                name="position"
                size={props.size ?? "small"}
                variant={props.variant ?? "outlined"}
                value={positions}
                onChange={(e) => handleChange(e.target.value)}
                fullWidth
                required
                disabled={positionsRedux.loading}
                InputProps={{
                    endAdornment: loading &&
                        <Typography sx={{ width: '100%', textAlign: 'right' }}>
                            {status}
                            <CircularProgress size={15} style={{ marginRight: 15 }} />
                        </Typography>
                }}
            >
                {positionsOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                        {option.label}
                    </MenuItem>
                ))}
            </TextField>

        </div>
    )
}

export default Positions;