
import React from 'react';
import toastService from '../../../services/toast/toastService';
import { useDispatch, useSelector } from 'react-redux';
import usersRepository from '../../../repositories/usersRepository';
// MUI Components
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import LoadingButton from '@mui/lab/LoadingButton';
// Form Components
import Branch from './fields/Branch';
// react-icons
import { FaCheck } from 'react-icons/fa';
import { setNewBranch } from '../../../redux/slices/employeeSlice';


export default function ChangeBranchForm(props) {

    const dispatch = useDispatch();
    const usersRedux = useSelector((state) => state.users.value); 

    const [branch, setBranch] = React.useState(usersRedux.selectedUser?.role_id ?? "");
    const [label, setLabel] = React.useState("");
    const [loading, setLoading] = React.useState(false);

    const uuid = props?.selectedData?.uuid;
    const index = props?.selectedData?.index;

    const handleSubmit = async (e) => {
        try {
            e.preventDefault();

            setLoading(true);

            let data = {
                branch: label,
                branch_id: branch,
                uuid: uuid,
                index: index
            }

            let result = await usersRepository.changeBranch(data);

            if (!result.error) { 
                dispatch(setNewBranch(data));
                props.toggle();
            }

        } catch (e) {
            toastService.error(e.toString());
        } finally {
            setLoading(false)
        }

    }

    return (
        <div style={{ minWidth: 200 }}>
            <Paper sx={{ p: 0, m: 0, mt: 1 }} elevation={0}>
                <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
                    <Grid container spacing={2} >
                        <Grid item xs={12} sx={{ minWidth: 500 }}>
                            <Branch branch={branch} setBranch={setBranch} setLabel={setLabel} />
                        </Grid>
                        <Grid item xs={12}>
                            <LoadingButton
                                type="submit"
                                fullWidth
                                variant="contained"
                                color="primary"
                                loading={loading}
                                disabled={loading}
                            >
                                Change
                                <FaCheck style={{ marginLeft: 4 }} />
                            </LoadingButton>
                        </Grid>
                    </Grid>
                </form>
            </Paper>
        </div>
    )
}
