import React from 'react';
import utilityService from '../../../../services/utils/utilityServices';
// Redux
import { useDispatch, useSelector } from 'react-redux';
import { get } from '../../../../redux/async/rolesAsync';
// MUI Components
import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem';
import { Typography, CircularProgress } from '@mui/material';
// Page Component
import LinearProgressBar from '../../shared/LinearProgressBar/LinearProgressBar';



const Roles = (props) => {

    const dispatch = useDispatch();
    const rolesRedux = useSelector((state) => state.roles.value);
    const [roles, setRoles] = React.useState(props.roles ?? "");

    const loading = rolesRedux.loading;
    const status = rolesRedux.status;

    const roleOptions = rolesRedux.roles.map(data => {
        return {
            value: data.id,
            label: utilityService.capitalizeFirstLetter(data.role)
        }
    });

    const handleChange = (value) => {

        setRoles(value)
        if (props.setRoles) {
            props.setRoles(value)
        }

        if (props.setLabel) {
            let label = rolesRedux.roles.filter(data => {
                return value === data.id;
            });

            props.setLabel(label[0].role)
        }
    }

    React.useEffect(() => { 
        dispatch(get());
    }, [dispatch])

    return (
        <div> 
            <TextField
                select={!loading}
                label={props.label ?? "Roles"}
                name="role"
                size="small"
                variant={props.variant ?? "outlined"}
                value={roles}
                onChange={(e) => handleChange(e.target.value)}
                fullWidth
                required
                disabled={loading}
                InputProps={{
                    endAdornment: loading &&
                        <Typography sx={{ width: '100%', textAlign: 'right' }}>
                            {status}
                            <CircularProgress size={15} style={{ marginRight: 15 }} />
                        </Typography>
                }}
            >
                {roleOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                        {option.label}
                    </MenuItem>
                ))}
            </TextField>

        </div>
    )
}

export default Roles;