import React from 'react';
import { Avatar, Grid } from '@mui/material';
import config from '../../../config/config';
import EmployeeProfileForm from '../forms/EmployeeProfileForm';
import LargeModalDialog from '../shared/Dialog/LargeModalDialog';

export default function EmployeeProfileModal(props) {

    const employee = props.selectedData ?? null;
    const avatar = employee?.data?.user?.avatar;
    let avatar_path = "";

    if (employee !== null && avatar !== null && avatar !== undefined) {
        avatar_path = `${config.RESOURCES}users/${employee?.uuid}/files/avatar/${avatar}`;
    }

    return (
        <div>
            <LargeModalDialog open={props.open} title={`Employee Profile`} toggle={props.toggle} size="lg">
                <Grid container>
                    <Grid item xs={2.5}>
                        <Avatar src={avatar_path} sx={{ p: 2, width: 230, height: 220 }} variant="square" />
                    </Grid>
                    <Grid item xs={9.5}>
                        <EmployeeProfileForm selectedEmployee={props.selectedData} />
                    </Grid>
                </Grid>
            </LargeModalDialog>
        </div>
    )
}
