import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setNewPosition } from '../../../redux/slices/employeeSlice';
import employeeRepository from '../../../repositories/employeeRepository';
import toastService from '../../../services/toast/toastService';
// MUI Components
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import LoadingButton from '@mui/lab/LoadingButton';
// Form Components
import Positions from './fields/Positions';
// react-icons
import { FaCheck } from 'react-icons/fa';

export default function ChangePositionForm(props) {

    const dispatch = useDispatch();
    const usersRedux = useSelector((state) => state.users.value); 

    const [positions, setPositions] = React.useState(usersRedux.selectedUser?.position_id ?? "");
    const [label, setLabel] = React.useState("");
    const [loading, setLoading] = React.useState(false);

    const uuid = props?.selectedData?.uuid;
    const index = props?.selectedData?.index;

    const handleSubmit = async (e) => {
        try {
            e.preventDefault();

            setLoading(true);

            let data = {
                position: label,
                position_id: positions,
                uuid: uuid,
                index: index
            }

            let result = await employeeRepository.changePosition(data);

            if (!result.error) {
                dispatch(setNewPosition(data));
                props.toggle();
            }

        } catch (e) {
            toastService.error(e.toString());
        } finally {
            setLoading(false)
        }

    }

    return (
        <div style={{ minWidth: 200 }}>
            <Paper sx={{ p: 0, m: 0, mt: 1 }} elevation={0}>
                <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
                    <Grid container spacing={2} >
                        <Grid item xs={12} sx={{ minWidth: 500 }}>
                            <Positions positions={positions} setPositions={setPositions} setLabel={setLabel} />
                        </Grid>
                        <Grid item xs={12}>
                            <LoadingButton
                                type="submit"
                                fullWidth
                                variant="contained"
                                color="primary"
                                loading={loading} 
                            >
                                Change
                                <FaCheck style={{ marginLeft: 4 }} />
                            </LoadingButton>
                        </Grid>
                    </Grid>
                </form>
            </Paper>
        </div>
    )
}
