import React from 'react';
import toastService from '../../../../services/toast/toastService';
// Repository
import authRepository from '../../../../repositories/authRepository';
import TextField from '@mui/material/TextField';
import InputAdornment from "@mui/material/InputAdornment";
// react-icons
import { FaTimes, FaCheck, FaLock } from 'react-icons/fa';
import { CircularProgress, Typography } from '@mui/material';


export default function Email(props) {

    const [email, setEmail] = React.useState("");
    const [emailLoading, setEmailLoading] = React.useState(false);
    const [emailCheck, setEmailCheck] = React.useState(false);
    const [emailError, setEmailError] = React.useState(false);

    const handleChange = (value) => {
        setEmail(value);

        if (props.setEmail) {
            props.setEmail(value);
        }
    }

    const handleEnter = (e) => {
        try {
            if (e.key === "Enter") {
                handleCheckEmail();
            }
        } catch (e) {
            setEmailLoading(false);
            toastService.error(e);
        }
    }

    const handleCheckEmail = async () => {

        try {

            if (email === "") {
                setEmailCheck(false);
                setEmailError(false);
                setEmailLoading(false);
                return false;
            }

            setEmailCheck(false);
            setEmailError(false);
            setEmailLoading(true);

            if (props.setEmailChecking) {
                props.setEmailChecking(true);
            }

            let data = {
                email
            }

            let result = await authRepository.verifyEmail(data);
            if (result.data.status) {
                setEmailCheck(true);
                if(props.setEmailCheck){
                    props.setEmailCheck(true);
                }
            } else {
                if(props.setEmailCheck){
                    props.setEmailCheck(false);
                }
                setEmailError(true);
            }

            if (props.setIsEmailCorrect) {
                props.setIsEmailCorrect(result.data.status);
            }

            setEmailLoading(false);

        } catch (error) {
            setEmailLoading(false);
            toastService.error(error);
        }

        if (props.setEmailChecking) {
            props.setEmailChecking(false);
        }
    }

    React.useEffect(() => {
        setEmail(props.email);
    }, [props.email])

    return (
        <TextField
            onChange={(e) => handleChange(e.target.value)}
            onBlur={() => !props.lock ? handleCheckEmail() : null}
            onKeyDownCapture={(e) => handleEnter(e)}
            value={email}
            error={emailError}
            variant={props.variant ?? "outlined"}
            size={props.size ?? "small"}
            required
            fullWidth
            id="email"
            label="Email"
            name="email"
            type="email"
            helperText={emailError ? "Email is invalid or already in use" : !props.lock ? props.helperText : "Only owner can update"}
            InputProps={{
                endAdornment: (
                    <InputAdornment position="end">
                        {props.lock && <FaLock color='#000' />}
                        {!props.lock && emailLoading && <Typography>Checking... <CircularProgress size={15} /></Typography>}
                        {!props.lock && emailError && <FaTimes color='#E45570' />}
                        {!props.lock && emailCheck && <FaCheck color='#00BFA5' />}
                    </InputAdornment>
                ),
            }}
            inputProps={{
                readOnly: props.lock ?? false
            }}
        />
    )
}
