import React from 'react'
// Modal Components
import InnerModalDialog from '../shared/Dialog/InnerModalDialog';
import ChangeRoleForm from '../forms/ChangeRoleForm';

export default function ChangeRoleModal(props) { 
    return (
        <div>
            <InnerModalDialog open={props.open} title={"Change Role"} toggle={props.toggle}>
                <ChangeRoleForm toggle={props.toggle} selectedData={props.selectedData} />
            </InnerModalDialog>
        </div>
    )
}
